.app {
  font-size: 1.6rem;
}
@media screen and (max-width: 960px) {
  .app {
    font-size: 1.4rem;
  }
}
.app .aside {
  position: fixed;
  left: 0;
  height: 100vh;
  overflow-y: auto;
}
.app .aside .logo {
  height: 3rem;
  margin: 1rem;
  box-sizing: border-box;
  border-radius: 1rem;
  padding: 0 1rem;
  text-align: center;
}
.app .aside .logo img {
  height: 100%;
}
.app .header {
  background-color: #fff;
  padding: 0 1.5rem;
  display: flex;
  justify-content: space-between;
}
.app .header .right {
  display: flex;
  align-items: center;
}
.app .content {
  padding: 10px 18px;
  width: auto !important;
}
.app .footer {
  text-align: center;
}